button, input, textarea {
  -webkit-appearance: none;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
}

html {
  box-sizing: border-box;
  height: 100%;
  overflow-x: hidden;
  font-family: 'Fira Sans', sans-serif;
  font-size: @font_size;
  line-height: @line_height;
}

*, *::after, *::before {
  box-sizing: inherit;
}

body {

  min-height: 100%;
  position: relative;
  -webkit-font-smoothing: antialiased;
}

.clearfix::after {
  content: '';
  display: block;
  clear: both;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a, .link {
  text-decoration: none;
  color: inherit;
  transition: all .5s;
}

a:link, .link:link {

}

a:visited, .link:visited {

}

a:hover, .link:hover {

}

a:focus, .link:focus {
  outline: none;
}

a:active, .link:active {

}

.site-icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.site-icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.pseudo-full-width-background::after {
  content: '';
  display: block;
  position: absolute;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  top: 0;
  left: -50vw;
  right: -50vw;
  margin-right: auto;
  margin-left: auto;
  z-index: -1;
}

.wrapper {
  position: relative;
  max-width: 1270px;
  min-width: 320px;
  margin: auto;
  padding: 0 calc(1vw + .2rem);
}

@supports (padding: 0) {
  .wrapper {
    padding-left: max(15px, env(safe-area-insert-left));
    padding-right: max(15px, env(safe-area-insert-right));
  }
}

.fade-in {
  animation-name: fadeIn;
  animation-duration: 1.3s;
  animation-timing-function: cubic-bezier(0, 0, 0.4, 1);
  animation-fill-mode: forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}































